.footer {
    background-color: #1a4d6c;
    padding: 30px 30px 15px;
    color: white;
    font-family: 'Segoe UI', sans-serif;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.15);
    border-bottom: 2px solid #368a91;
  }
  
  .footer-content {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    gap: 20px;
    margin-bottom: 20px;
  }
  
  .footer-logo {
    max-width: 200px;
    text-align: center;
  }
  
  .footer-logo-img {
    width: 100%;
    max-width: 190px;
  }
  
  .slogan {
    font-size: 20px;
    margin-top: 10px;
    color: #b0d5c4;
  }
  
  .footer-info, .footer-about, .footer-social, .footer-newsletter {
    max-width: 250px;
  }
  
  .footer-info a, .footer-about p, .footer-social a {
    color: #d0f0e0;
    font-size: 17px;
    text-decoration: none;
  }
  
  .footer-info a:hover, .footer-social a:hover {
    color: #66bb6a;
  }
  
  .footer-about h3, .footer-social h3, .footer-newsletter h3 {
    font-size: 18px;
    border-bottom: 2px solid #66bb6a;
    padding-bottom: 5px;
  }
  
  .social-icons a {
    font-size: 18px;
    margin-right: 12px;
    color: white;
    transition: color 0.3s, transform 0.3s;
  }
  
  .social-icons a:hover {
    color: #66bb6a;
    transform: scale(1.1);
  }
  
  .footer-newsletter input {
    width: 100%;
    padding: 5px;
    margin: 5px 0;
    border-radius: 5px;
    border: none;
  }
  
  .footer-newsletter button {
    padding: 10px 20px;
    background-color: #66bb6a;
    color: white;
    font-weight: bold;
    border: none;
    border-radius: 10px;
    cursor: pointer;
    transition: background-color 0.3s;
  }
  
  .footer-newsletter button:hover {
    background-color: #4caf50;
  }
  
  .footer-bottom {
    text-align: center;
    font-size: 14px;
    color: #b0d5c4;
    border-top: 1px solid #4b7d68;
    padding-top: 10px;

  }

  